	module.exports = {"data":{"markdownRemark":{"html":"<p>Having a flawless skin is the dream of many women in this world including me, of course! However, I donâ€™t know why recently I got so many pimples and after they gone, the left acne scars on my face. ðŸ˜­ ðŸ˜­ ðŸ˜­</p>\n<p>OMG, I hate those scars and I want to get rid of them. Since they make me so unconfident when going out, I decided to hide them away first before removing them completely. Thatâ€™s the reason why today, Iâ€™m gonna talk about <strong>Innisfree Mineral Essential Concealer SPF30 PA++ 6.5g</strong>.</p>\n<figure style=\"width: 500px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/concealer-1-ec995db97afebd87b7ed0beb2e7bbebb-5d1ec.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 600px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 97.33333333333333%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"concealer 1\" title=\"\" src=\"/static/concealer-1-ec995db97afebd87b7ed0beb2e7bbebb-5d1ec.png\" srcset=\"/static/concealer-1-ec995db97afebd87b7ed0beb2e7bbebb-81040.png 300w,\n/static/concealer-1-ec995db97afebd87b7ed0beb2e7bbebb-5d1ec.png 600w\" sizes=\"(max-width: 600px) 100vw, 600px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p>Itâ€™s sold at 6,000krw (~$6) but I got this concealer at Innisfree store with their 50% sale event so it was just 3,000krw (~$3). And, there are providing 3 shades.</p>\n<figure style=\"width: 500px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/concealer-2-508087d1154e4d7d68fa63de247d22c2-4aff4.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 609px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 41.050903119868636%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"concealer 2\" title=\"\" src=\"/static/concealer-2-508087d1154e4d7d68fa63de247d22c2-4aff4.png\" srcset=\"/static/concealer-2-508087d1154e4d7d68fa63de247d22c2-45fe6.png 300w,\n/static/concealer-2-508087d1154e4d7d68fa63de247d22c2-2142f.png 600w,\n/static/concealer-2-508087d1154e4d7d68fa63de247d22c2-4aff4.png 609w\" sizes=\"(max-width: 609px) 100vw, 609px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p>My experience of buying concealer is that you should choose the color that 1 tone brighter than your skin tone. My skin is not that bright and my purpose is to cover the pimples and acne scars, so I chose the #2 â€“ <strong>Natural Beige</strong>.</p>\n<h2 id=\"package\"><a href=\"#package\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Package</h2>\n<p>Extremely simple. It has no paper box and the seal between the lid and bottle body is quite good. Everything is written in Korean.</p>\n<h2 id=\"ingredients\"><a href=\"#ingredients\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Ingredients</h2>\n<p>Since there is no English, I just translate the ingredients from Korean.</p>\n<p><span style=\"color:royalblue\"> Cyclopentasiloxane, titanium dioxide, purified water, phenyltrimethicone, talc, butyleneglycol, cetylpiperazine/piperazine-10/1 dimethicone, 10-dimethicone, niacinamide, zinc oxide, dimethicone / vinyl dimethicone cross polymer, lauryl pyridine-9 polydimethylsiloxy ethyl cholate dimethicone, sodium chloride, mash extract, green tea extract, , Camellia leaf extract, tangerine peel extract, mineral salt, distearammonium hectorite, beeswax, triethoxycaprilylsilane, panthenol, hydroxyacetophenone, palmitic acid, stearic acid, Polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, polyoxyethylene stearate, benzoate, disodium a-dithiane A, 1,2-hexanediol, red iron oxide, yellow iron oxide, black iron oxide, perfume </span></p>\n<p><strong>Innisfree claims</strong> that Mineral Essential Concealer is not only a product with SPF30 PA++, but also has <strong>anti-wrinkle, whitening, coverage and moisturizing effect for all skin type (especially dry skin)</strong>.</p>\n<h2 id=\"how-to-use\"><a href=\"#how-to-use\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How to use</h2>\n<p>I usually apply it after the cushion and use my hand or the puff to spread it. But I think that it will look more natural if you put it after the sunblock and before the cushion. (I didnâ€™t use foundation or base. So lazy to use all of them)</p>\n<p><strong>Skincare -> Sunblock -> Cushion -> Concealer</strong></p>\n<figure style=\"width: 400px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/concealer-3-5ae5485b8619939626981e6b1cad54c7-cabc9.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 530px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 72.45283018867926%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"concealer 3\" title=\"\" src=\"/static/concealer-3-5ae5485b8619939626981e6b1cad54c7-cabc9.png\" srcset=\"/static/concealer-3-5ae5485b8619939626981e6b1cad54c7-a4775.png 300w,\n/static/concealer-3-5ae5485b8619939626981e6b1cad54c7-cabc9.png 530w\" sizes=\"(max-width: 530px) 100vw, 530px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p><strong>How it works for me</strong></p>\n<figure style=\"width: 700px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-7b2b1.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 31.092436974789916%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"concealer 4\" title=\"\" src=\"/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-abe1c.png\" srcset=\"/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-10046.png 300w,\n/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-3c4a3.png 600w,\n/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-abe1c.png 1200w,\n/static/concealer-4-6e340e4d141b8f0338439bcbf68d4df7-7b2b1.png 1309w\" sizes=\"(max-width: 1200px) 100vw, 1200px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p><strong>I say</strong></p>\n<p>It is ok for that cheap price. With only one layer, it just covers well my scars, not the pimples, so I put it 2-3 layers. Moreover, after using for hours, my skin becomes dry at the area where I put on the concealer (it's probably due to the dry weather in Korea). I have no idea about anti-wrinkle efect. I donâ€™t use it for the dark circle or wrinkles under my eyes because I am afraid that it''ll make the wrinkles even more serious.</p>\n<p>So, if you already have a smooth skin with minor problems such as scars, this product is good and cheap. But if you need something hidden pimples perfectly, you should find another concealer. I will try another when I finish it. </p>","timeToRead":3,"excerpt":"Having a flawless skin is the dream of many women in this world including me, of course! However, I donâ€™t know why recently I got so manyâ€¦","frontmatter":{"title":"Review - Innisfree Mineral Essential Concealer SPF30","cover":"/images/in2.png","date":"2016/05/05","category":"Beauty","tags":["Beauty","concealer","base-makeup","innisfree","korean-cosmetic","kem-che-khuyet-diem","trang-diem","my-pham-han-quoc"]},"fields":{"slug":"/en/Review-Innisfree-Mineral-Essential-Concealer-SPF30/"}}},"pathContext":{"path":"/en/Review-Innisfree-Mineral-Essential-Concealer-SPF30/","langKey":"en"}}

